import Cookies from 'js-cookie';
import React, { useCallback, useEffect, useState } from 'react';
import AnnoucementBar from './AnnouncementBar';
import styles from './styles.module.scss';

type Props = {
  selected: number;
};

const LinkData = [
  {
    href: '/',
    name: 'Home',
  },
  {
    href: '/about',
    name: 'About',
  },
  {
    href: 'mailto:founders@twelvelabs.io',
    name: 'Contact Us',
  },
];

const Header: React.FC<Props> = ({ selected }) => {
  const [showAnnoucement, setShowAnnoucement] = useState<boolean>(false);

  useEffect(() => {
    const show = Cookies.get('showAnnoucement') ?? 'true';
    setShowAnnoucement(show === 'true' ? true : false);
  }, []);

  const closeClick = useCallback(() => {
    Cookies.set('showAnnoucement', 'false', { expires: 1 });
    setShowAnnoucement(false);
  }, []);

  return (
    <>
      {showAnnoucement && (
        <AnnoucementBar
          link="https://arxiv.org/abs/2110.05146"
          text="Twelvelabs won 1st place in ICCV 2021 ðŸŽ‰"
          prefixTag={<div className={styles.newsBtn}>NEWS</div>}
          onClose={closeClick}
        />
      )}
      <div className={styles.root}>
        <div>
          <a href="/">
            <img src="/logo192.png" alt="logo" />
          </a>
        </div>
        <div className={styles.anchors}>
          {LinkData.map(({ href, name }, idx) => {
            return (
              <a
                href={href}
                key={href}
                className={idx === selected ? styles.selected : ''}
              >
                {name}
              </a>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default Header;
